<template>
	<!-- tab组件容器 -->
	<view 
	:style="{'top':(isTabFixed?offsetTop:'0'),'background-color':backgroundColor}"
	class="tabBar-container"
	:class="[isTabFixed?'tabBar-container-fixed':'']">
	<!-- tabs元素 -->
		<view class="common-tabBar" 
		:class="[activeTabIndex===index?'common-tabBar-active':'']"
			@tap="activeTabIndexChange(index)" v-for="(item,index) in tabs" :key="index">{{item}}
		</view>
		<!-- 装饰条 -->
		<view 
		:style="{'transform': `translate(${(750-15*2)/tabsLen*activeTabIndex}rpx)`}"
		class="animationBar"
		></view>
	</view>
</template>

<script>
	export default {
		name:'MyTab',
		props:{
			tabs:{
				type: Array,
				default: ['标题一','标题二','标题三']
			},
			isTabFixed:{
				type:Boolean,
				default:false
			},
			offsetTop:{
				type:String,
				default:'0rpx'
			},
			backgroundColor:{
				type:String,
				default:'white'
			},
			activeTabIndex:{
				type:Number,
				default:0
			}
		},
		computed:{
			tabsLen(){
				return this.$props.tabs.length
			}
		},
		data(){
			return{
			}
		},
		methods:{
			activeTabIndexChange(index){
				// console.log(index)
				this.$emit('activeTabIndexChange',index)
			}
		}
	}
</script>

<style lang="scss">
	$h: 80rpx;
	.tabBar-container {
		position: relative;
		display: flex;
		justify-content: space-around;
		box-sizing: border-box;
		width: 750rpx;
		padding: 15rpx;
		align-items: center;
		height: $h;
		&-fixed {
			position: sticky;
			z-index: 100;
			// 不同终端top位置不同
			// top: 0rpx;
			border-bottom: 1px solid #ddd;
			box-shadow: 0px 1px 3px #eee;
		}
	
		.common-tabBar {
			font-size: 27rpx;
			box-sizing: border-box;
			transition: all 0.1s ease;
	
			&-active {
				font-weight: bold;
				font-size: 30rpx;
			}
		}
		.animationBar{
			position: absolute;
			top: $h;
			left: 30rpx;
			background-color: red;
			width: 90rpx;
			height: 3rpx;
			transition: all 0.1s ease;
		}
	}
</style>
